/*------------------------------------*\
  #VARIABLES
\*------------------------------------*/
/* ==================================================
   Mixins
   ================================================== */
/*------------------------------------*\
  #BASE
\*------------------------------------*/
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
table td {
  padding: 20px !important;
}

@media (min-width: 820px) {
  .iframe iframe {
    width: 810px;
  }
}

p {
  margin: 0 0 20px 0;
}

::selection {
  background: #EF3340;
  color: #fff;
}

::-moz-selection {
  background: #fff;
  color: #fff;
}

body {
  margin: 0;
  color: #383b38;
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.6rem;
  line-height: 1.5;
}

html:not(.touch) {
  text-rendering: optimizeLegibility;
}

html.touch {
  text-rendering: optimizeSpeed;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  max-width: 100%;
  height: auto;
}

a {
  color: #030000;
  text-decoration: none;
}

strong {
  font-weight: 700;
}

ul {
  padding-left: 0;
  margin: 0;
}

li {
  list-style-type: none !important;
}

figure {
  margin: 0;
  max-width: 100%;
}

.text-main {
  line-height: 1.7;
  padding: 0;
}
@media (min-width: 700px) {
  .text-main {
    padding: 0 80px;
  }
}
.text-main h3 {
  font-size: 22px;
  margin-top: 0;
  margin-bottom: 8px;
}
.text-main p {
  margin-top: 0;
  margin-bottom: 48px;
}
.text-main ul {
  padding-left: 30px;
}
.text-main ul li:before {
  content: "";
  width: 13px;
  height: 2px;
  background: red;
  display: inline-block;
  transform: translate(-20px, -5px);
  margin-left: -13px;
}

/*------------------------------------*\
  #HEADINGS
\*------------------------------------*/
.h1 {
  margin-top: 0;
  margin-bottom: 0;
  line-height: 1.2;
  /*62*/
  font-weight: 500;
}
.h1 {
  font-size: 34px;
}
@media screen and (min-width: 320px) {
  .h1 {
    font-size: calc(34px + 18 * ((100vw - 320px) / 1050));
  }
}
@media screen and (min-width: 1370px) {
  .h1 {
    font-size: 52px;
  }
}

.h2 {
  margin-top: 0;
  margin-bottom: 0;
  line-height: 1.2;
  /*54*/
