/*------------------------------------*\
  #INPUTS
\*------------------------------------*/
input[type=text],
input[type=email],
input[type=tel],
input[type=password] {
  display: block;
  width: 100%;
  padding: 8px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.36;
  font-family: inherit;
  border: 0;
  border-bottom: 1px solid #979797;
}

textarea {
  width: 100%;
  padding: 8px;
  resize: none;
  border: 0;
  border-bottom: 1px solid #979797;
  min-height: 80px;
  font-family: inherit;
}

select {
  width: 100%;
  border: 0;
  border-bottom: 1px solid #979797;
  font-family: inherit;
}

input[type=submit] {
  display: inline-block;
  margin-top: 32px;
  border: 1px solid #485294;
  border-radius: 4px;
  font-size: 14px;
  font-size: 1.4rem;
  font-family: inherit;
  cursor: pointer;
  text-align: center;
  padding: 10px 16px 11px;
  background-color: #485294;
  color: #fff;
  transition: background-color 150ms linear;
}
@media (min-width: 700px) {
  input[type=submit] {
    padding: 14px 34px 15px;
  }
}

.form * {
  outline: none;
}

/*btn classes*/
.btn {
  display: inline-block;
  border: 1px solid #485294;
  border-radius: 4px;
  font-size: 14px;
  font-size: 1.4rem;
  font-family: inherit;
  cursor: pointer;
  text-align: center;
}

.btn--default {
  padding: 10px 16px 11px;
}
@media (min-width: 700px) {
  .btn--default {
    padding: 14px 34px 15px;
  }
}

.btn--dark {
  background-color: #485294;
  color: #fff;
  transition: background-color 150ms linear;
}

.btn--light {
  background-color: rgba(0, 59, 77, 0.2);
  color: #485294;
  transition: background-color 150ms linear, color 150ms linear;
}

.btn--white {
  background-color: #fff;
  color: #383b38;
  font-weight: 500;
  border-color: #fff;
  transition: background-color 150ms linear, color 150ms linear;
}

.btn--ghost {
  background-color: transparent;
  color: #fff;
  border-color: #fff;
  transition: background-color 150ms linear, color 150ms linear, border-color 150ms linear;
}

.btn--ghost-rev {
  background-color: transparent;
  color: #485294;
  border-color: #485294;
  transition: background-color 150ms linear, color 150ms linear, border-color 150ms linear;
}

.btn--icon {
  padding: 32px 34px 32px;
}

