.btn--c-t-c {
  padding: 13px 17px 13px;
  color: #ef3340;
}
.btn--c-t-c svg {
  max-width: 26px;
  margin: 0 10px -8px -2px;
}

.no-touch .btn--c-t-c:hover path {
  fill: #fff;
}

.btn--c-t-call {
  margin-right: 10px;
  padding: 14px 17px;
}
