/*checkboxes*/
input[type=checkbox] {
  float: left;
  margin-top: 7px;
  display: none;
}
input[type=checkbox] + label {
  position: relative;
  padding: 0 30px;
  cursor: pointer;
}
input[type=checkbox] + label:before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: 20px;
  height: 20px;
  border-radius: 3px;
  border: 1px solid #EF3340;
}
input[type=checkbox] + label:after {
  content: "";
  position: absolute;
  top: 1px;
  left: 3px;
  width: 16px;
  height: 16px;
  border-radius: 3px;
  background-color: transparent;
  transition: background-color 150ms ease;
}
input[type=checkbox]:hover + label:after {
  background-color: #EF3340;
}
input[type=checkbox]:checked + label:after {
  background-color: #EF3340;
}
input[type=checkbox]:disabled + label:before {
  opacity: 0.7;
}

/*radio buttons*/
input[type=radio] {
  float: left;
  margin-top: 7px;
  display: none;
}
input[type=radio] + label {
  position: relative;
  padding: 0 30px;
  cursor: pointer;
}
input[type=radio] + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 16px;
  height: 16px;
  border: 1px solid #EF3340;
  border-radius: 50%;
  background-color: #fff;
  transition: background-color 150ms ease;
}
input[type=radio] + label:after {
  content: "";
  position: absolute;
  left: 3px;
  top: 3px;
  width: 12px;
  height: 12px;
  background-color: transparent;
  border-radius: 50%;
  transition: background-color 150ms ease;
}
input[type=radio]:hover + label:after {
  background-color: #EF3340;
}
input[type=radio]:checked + label:after {
  background-color: #EF3340;
}

