\*------------------------------------*/
.accordion > li {
  margin-bottom: 8px;
  list-style-type: none;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
  border-left: 6px solid #EF3340;
}
.accordion h3 {
  margin: 0;
  line-height: 1.2;
  font-size: 20px;
  font-size: 2rem;
}
.accordion input[name=panel] {
  display: none;
}
.accordion label {
  position: relative;
  width: 100%;
  display: block;
  margin-bottom: 24px;
  padding: 24px 24px 0;
  line-height: 1.1;
  font-size: 18px;
  font-weight: 400;
  transition: font-weight 3000ms ease-in-out;
}
.accordion input[type=checkbox] + label {
  padding: 24px 74px 0 24px;
}
.accordion input[type=checkbox] + label:after, .accordion input[type=checkbox] + label:before {
  display: none;
}
.accordion label:after {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  top: 5px;
  right: 10px;
  background-image: url(../images/icon-arrow.svg);
  background-repeat: no-repeat;
  background-position: 8px 11px;
}
.accordion input:checked + label {
  font-weight: 500;
}
.accordion input:checked + label:after {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.accordion--glossary > li {
  box-shadow: 0 0;
  border-radius: 0;
  border: none;
  border-bottom: 2px solid rgba(181, 181, 181, 0.4);
}
.accordion--glossary input[type=checkbox] + label {
  padding: 16px 24px 0;
}
.accordion--glossary .accordion__state {
  border-color: #ff303d;
}
.accordion--glossary .accordion__state:before, .accordion--glossary .accordion__state:after {
  background: #ff303d;
}

.accordion__content {
  overflow: hidden;
  max-height: 0em;
  position: relative;
  padding: 0 60px 0 24px;
  transition: all 500ms cubic-bezier(0.865, 0.14, 0.095, 0.87);
}
.accordion__content p {
  margin-bottom: 20px;
  line-height: 1.2;
}
.accordion__content li {
  margin-left: 20px;
}

input[name=panel]:checked ~ .accordion__content {
  max-height: 140em;
}

