}
.list-grid article {
  position: relative;
  break-inside: avoid-column;
  border-radius: 5px;
  border: 1px solid #dedede;
  will-change: box-shadow;
  page-break-inside: avoid;
  transition: box-shadow 300ms ease, border-color 200ms ease;
  box-shadow: 0 15px 35px 0 rgba(0, 0, 0, 0);
