/*------------------------------------*\
  #PRENEXT
\*------------------------------------*/
.siblings-wrap {
  position: relative;
  overflow: hidden;
  padding: 48px 0 78px;
  background-color: #ecedf4;
}

.siblings {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 700px) {
  .siblings {
    flex-direction: column;
  }
}

.siblings__label {
  margin-bottom: 8px;
  font-size: 14px;
  color: #485294;
}

@media (min-width: 700px) {
  .siblings__prev, .siblings__next {
    flex-basis: 48%;
    max-width: 320px;
  }
}

@media (max-width: 700px) {
  .siblings__prev {
    margin-bottom: 24px;
  }
}
@media (min-width: 700px) {
  .siblings__prev .siblings__label {
    margin-left: 51px;
  }
}

.siblings__title a {
  display: flex;
  color: #485294;
  transition: color 300ms ease;
}

.siblings__next__arrow {
  min-width: 35px;
  margin-left: 16px;
}

.siblings__prev__arrow {
  min-width: 35px;
  margin-right: 16px;
}

.siblings img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
  opacity: 0;
  transition: all 300ms ease;
  object-fit: cover;
  height: 100%;
}

.siblings__hover:hover .siblings__label {
  z-index: 3;
  position: relative;
  color: #fff;
}
.siblings__hover:hover .siblings__title + picture img {
  z-index: 1;
  opacity: 1;
}
.siblings__hover:hover .siblings__title:after {
  content: "";
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
}
.siblings__hover:hover .siblings__title a {
  color: #fff;
  z-index: 3;
  position: relative;
}
.siblings__hover:hover .siblings__title path {
  fill: #fff;
}

/*------------------------------------*\
  #TABS
\*------------------------------------*/
.tab {
  position: relative;
  padding: 0 42px 20px 42px;
  display: inline-block;
  margin-right: 10px;
  font-size: 18px;
  overflow: hidden;
  vertical-align: bottom;
}
.tab:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #EF3340;
  transform: translateX(-101%);
  transition: transform 300ms ease-out;
}

.anchorlist-narrow .tab {
  padding: 0 15px 20px 15px;
}

.tab--active {
  font-weight: 500;
}
.tab--active:after {
  transform: translateX(0);
}

.no-touch .tab:hover:after {
  transform: translateX(0);
}

.scroll-items {
  position: relative;
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  user-select: none;
  cursor: pointer;
  text-align: left;
}

.scroll-items.active {
  background: rgba(255, 255, 255, 0.3);
  cursor: grabbing;
  cursor: -webkit-grabbing;
}

.tab-separator {
  height: 28px;
  width: 1px;
  background-color: #000;
  display: inline-block;
  vertical-align: top;
}

