/* ==================================================
   Mixins
   ================================================== */
/*------------------------------------*\
  #VARIABLES
\*------------------------------------*/
.hero {
  margin-right: 16px;
}
@media (min-width: 1140px) {
  .hero {
    margin-right: 24px;
  }
}
@media (min-width: 1400px) {
  .hero {
    margin-right: 60px;
  }
}
@media (min-width: 1700px) {
  .hero {
    margin-right: calc( (100vw - 1700px ) / 2 + 53px);
  }
}
.hero img {
  width: 100%;
  display: block;
  border-radius: 0 0 100px 0;
  max-height: 576px;
  object-fit: cover;
}

/*hero with extras*/
.hero-contents {
  position: absolute;
  top: 0;
  border: 0;
  width: 100%;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
  text-align: center;
  height: 100%;
  justify-content: center;
}
@media (min-width: 1700px) {
  .hero-contents {
    padding-left: calc((100vw - 1580px) / 2);
  }
}
.hero-contents:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 0 0 100px 0;
}
.hero-contents h1 {
  color: #fff;
}
.hero-contents p {
  font-size: 18px;
  color: #fff;
}

.hero-contents__sup {
  color: #fff;
  margin-bottom: 34px;
  font-size: 18px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}
.hero-contents__sup:after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 1px;
  width: 80%;
  background-color: #EF3340;
}

.hero-contents--plain {
  padding-top: 70px;
  padding-bottom: 74px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 400px;
}
.hero-contents--plain:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
}
.hero-contents--plain h1 {
  color: #fff;
}
.hero-contents--plain p {
  font-size: 18px;
  color: #fff;
}

.hero-plain picture img {
  object-fit: cover;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
}

.hero--extra .h6 {
  font-weight: 500;
  color: #fff;
}

.hero-slider__item {
  position: relative;
  display: block;
  color: #fff;
}
.hero-slider__item .h6 {
  font-weight: 400;
}

.grouping-dots {
  position: absolute;
  z-index: 2;
  bottom: 50px;
  left: 0;
  padding-left: calc((100vw - 1580px) / 2);
  right: 0;
  width: 100%;
  text-align: center;
}
.grouping-dots li {
  position: relative;
  width: 47px;
  height: 47px;
  margin: 0 10px;
  display: inline-block;
  padding: 10px;
  border: 1px solid #fff;
  border-radius: 50%;
  cursor: pointer;
}
.grouping-dots li > a {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.grouping-dots li:after {
  opacity: 0;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transition: opacity 600ms ease-in-out;
}
.grouping-dots img {
  border-radius: 0;
}
.grouping-dots .slick-active {
  border-color: #ef3340;
  background-color: #ef3340;
}

.no-touch .grouping-dots li:hover:after {
  opacity: 1;
  box-shadow: 0 1px 0 0 rgba(242, 177, 0, 0.5), 0 -1px 0 0 rgba(165, 181, 222, 0.5), 1px 0 0 0 rgba(225, 131, 194, 0.5), -1px 0 0 0 rgba(33, 196, 181, 0.5), 1px -1px 0 0 rgba(195, 156, 208, 0.5), -1px 1px 0 0 rgba(195, 156, 208, 0.5), 1px 1px 0 0 #ff6bb4, -1px -1px 0 0 rgba(72, 82, 148, 0.5);
  animation: rotateThis 1s linear infinite;
}

@keyframes rotateThis {
  from {
    transform: rotate(0deg) scale(1);
  }
  to {
    transform: rotate(360deg) scale(1);
  }
}
