/*filtering*/
.filter {
  position: relative;
  margin-bottom: 30px;
  text-align: right;
}

.filter__trigger {
  display: inline-block;
  padding: 10px 14px 9px;
  border: 1px solid #485294;
  background-color: #485294;
  color: #fff;
  border-radius: 4px;
  font-size: 12px;
  font-family: inherit;
  cursor: pointer;
  text-align: center;
}

.filter__results {
  box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.18);
  position: absolute;
  top: 50px;
  right: 0;
  opacity: 0;
  padding: 24px;
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  z-index: -1;
  transform: translateY(40px);
  transition: opacity 300ms ease-in-out, transform 400ms ease-in-out, z-index 0.5s step-end;
  background-color: #fff;
}

.filter__results--active {
  opacity: 1;
  z-index: 1;
  transform: translateY(0);
  transition: opacity 300ms ease-in-out, transform 400ms ease-in-out, z-index 0.5s step-start;
}

.filter__results__column {
  flex-basis: 33%;
