  background-color: #fff;
  border-radius: 4px;
  height: 38px;
}

a.btn--ghost-header {
  font-size: 12px;
  color: #ff303d;
  font-weight: 500;
  padding: 10px 20px 10px;
  margin-right: 8px;
  background-color: #fff;
  border-radius: 4px;
}

.access__label {
  font-size: 12px;
  color: #fff;
  line-height: 1;
  font-weight: 500;
  border: 1px solid #fff;
  padding: 12px 35px 10px 20px;
  border-radius: 4px;
  position: relative;
  height: 38px;
  cursor: pointer;
}
.access__label svg {
  max-width: 16px;
  margin: 0 2px;
  position: absolute;
  top: 9px;
  right: 15px;
}

.access {
  position: relative;
}
.access:hover .access__list {
  display: block;
}
.access:hover .access__label {
  background: #fff;
  color: #EF3340;
}
.access:hover .access__label:after {
  content: "";
  background: #fff;
  position: absolute;
  bottom: -5px;
  width: 100%;
  height: 5px;
  left: -1px;
  border: 1px solid #fff;
}
.access:hover .access__label:before {
  content: "";
  background: #fff;
  position: absolute;
  top: -6px;
  width: 100%;
  height: 6px;
  left: -1px;
  border: 1px solid #fff;
}
.access:hover .access__label path {
  fill: #EF3340;
}

.access__list {
  position: absolute;
  top: 42px;
  display: none;
  background-color: #fff;
  width: 100%;
  box-shadow: 0 18px 17px 0px rgba(0, 0, 0, 0.1);
  font-size: 14px;
}
.access__list a {
  color: #383b38;
  padding: 10px 20px;
  display: block;
}
.access__list a:hover {
  background: #fdeaeb;
}

.nav-main__list > .sublisted > .sublist {
  position: absolute;
  top: 47px;
  min-width: 220px;
  display: none;
  box-shadow: 0 18px 17px 0px rgba(0, 0, 0, 0.1);
}
.nav-main__list > .sublisted:hover .sublist {
  display: block;
}

.sublist {
  background-color: #fff;
  width: 100%;
  font-size: 14px;
}
.sublist a {
  color: #383b38;
  padding: 10px 20px;
  display: block;
}
.sublist a:hover {
  background: #fdeaeb;
}
.sublist .active > a {
  font-weight: 800;
}

.sublisted {
  position: relative;
}
.sublisted > a:after {
  content: "";
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='60' height='60' viewBox='0 0 60 60'><defs><path id='menusvg' d='M41.118 32.08L23.029 50.137a2.948 2.948 0 0 1-4.166 0 2.938 2.938 0 0 1 0-4.159l16.005-15.98-16.005-15.978a2.938 2.938 0 0 1 0-4.16 2.95 2.95 0 0 1 4.166 0l18.09 18.06c.574.574.862 1.326.862 2.079a2.93 2.93 0 0 1-.863 2.08z'></path></defs><use fill='%23000' fill-rule='nonzero' transform='rotate(90 29.99 30)' xlink:href='%23menusvg'></use></svg>");
  background-repeat: no-repeat;
  background-size: 18px;
  background-position: 0 100%;
  display: inline-block;
  width: 18px;
  height: 18px;
  position: absolute;
  top: 0;
  right: -5px;
}
@media (min-width: 820px) {
  .sublisted > a:after {
    position: relative;
    top: 4px;
    left: 5px;
  }
}

.sublist .sublist {
  display: block;
}
.sublist .sublist a {
  padding-left: 40px;
}

/*

******************************
********* RESPONSIVE *********
******************************

*/
@media (max-width: 820px) {
  .menu-products {
    display: block;
    padding: 0;
  }
  .menu-products .menu-products__profesional {
    padding: 20px 20px 10px;
  }
  .menu-products .menu-products__personal, .menu-products .menu-products__profesional {
    display: block;
  }
  .menu-products .menu-products__personal {
    padding: 24px 0 48px 0;
  }
  .menu-products .menu-products__personal h3 {
    margin: 0 20px;
  }
  .menu-products .menu-products__personal ul {
    grid-template-columns: repeat(2, 1fr);
  }
  .menu-products .menu-products__personal ul a {
    width: auto;
  }

  .menu-switch {
    display: inline-block;
  }

  .header-mobile .search-switch {
    display: inline-block;
    opacity: 0;
    transform: translateY(-2px);
    margin-right: 10px;
    pointer-events: none;
  }
  .header-mobile .search-switch svg {
    max-width: none;
    width: 24px;
    height: 24px;
  }

  body.menu-open {
    overflow: hidden;
  }
  body.menu-open .header-mobile .search-switch {
    opacity: 1;
    pointer-events: auto;
  }

  .search-switch {
    display: none;
  }

  header {
    transform: translateY(-100%);
    transition: transform 500ms ease;
    position: fixed;
    background: #fff;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    display: block;
    overflow-y: auto;
  }
  header .nav-product__wrap {
    padding: 0;
  }
  header .nav-product__wrap.--retract > div, header .nav-product__wrap.--retract > a {
    display: none;
  }
  header .nav-product__wrap.--retract > div.nav-product__trigger {
    display: block;
  }
  header .nav-product__wrap a.btn--white-header {
    font-size: 14px;
    color: white;
    background-color: #485294;
    padding: 15px 30px;
    width: 100%;
    height: auto;
    margin: 0 20px 20px 20px;
    text-align: center;
  }
  header .nav-product--health {
    background-color: white !important;
  }
  header .nav-product--health .nav-product__trigger {
    margin: 0 0 20px 20px;
    color: #EF3340;
  }
  header .nav-product--health .nav-product__trigger svg {
    right: -25px;
  }
  header .nav-product--health .nav-product__trigger svg path {
    fill: #EF3340;
  }
  header .nav-main__in {
    transition: all 1s ease-in-out;
    overflow: hidden;
    max-height: 620px;
  }
  header .nav-main__in.--retract {
    max-height: 0;
    padding-bottom: 60px;
  }
  header .access {
    width: 100%;
    margin: 0 20px;
  }
  header .access .access__label {
    padding: 15px 30px;
    font-size: 14px;
    height: auto;
    color: #485294;
    border-color: #485294;
    text-align: center;
  }
  header .access .access__label svg {
    right: unset;
    margin-left: 5px;
    top: 13px;
  }
  header .access .access__label svg path {
    fill: #485294;
  }
  header .access:hover .access__label {
    background-color: white;
    color: #485294;
    border-color: transparent;
  }

  .nav-main__list {
    margin-top: 40px;
  }
  .nav-main__list li {
    margin: 24px 0;
  }

  .menu-switch__top, .menu-switch__bottom {
    transition: transform 300ms ease 100ms;
    transform: rotate(0);
    transform-origin: 100%;
  }

  .menu-switch__middle {
    opacity: 1;
    transition: opacity 300ms ease;
    transition-delay: 200ms;
  }

  .menu-open header {
    transform: translateY(0);
  }
  .menu-open .menu-switch__top {
    transform: rotate(-45deg);
  }
  .menu-open .menu-switch__bottom {
    transform: rotate(45deg);
  }
  .menu-open .menu-switch__middle {
    opacity: 0;
    transition-delay: 0ms;
  }

  .header-mobile {
    padding: 27px 16px;
    display: block;
    position: relative;
    z-index: 11;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
  }

  .nav-main__list ul.sublist {
    max-height: 0;
    overflow: hidden;
    transition: 0.5s max-height ease-in-out;
  }
  .nav-main__list ul.sublist a:hover {
    background-color: transparent;
  }
  .nav-main__list ul.sublist.active {
    max-height: 300px;
  }
  .nav-main__list ul.sublist li:last-child {
    margin-bottom: 0;
  }
  .nav-main__list ul.sublist ul.sublist {
    margin-left: 20px;
  }
  .nav-main__list > li.sublisted > ul.sublist {
    top: 0;
    position: relative;
    box-shadow: none;
  }
  .nav-main__list > li.sublisted > ul.sublist a {
    padding: 0 20px;
  }

  .nav-support {
    order: 2;
    margin: 40px 0 0;
    display: block;
    width: 100%;
  }
  .nav-support .nav-support__label, .nav-support .nav-support__phone {
    display: none;
  }
  .nav-support .nav-support__button {
    display: block;
    font-size: 14px;
    color: white;
    background-color: #485294;
    padding: 15px 30px;
    width: 100%;
    height: auto;
    text-align: center;
  }
}
