/*------------------------------------*\
  #PARTNERS
\*------------------------------------*/

.list--partners__item{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding:18px 24px 24px 16px;
  border-top:1px solid rgba(216, 216, 216, 0.5);
}

.list--partners__name{
  font-size: 18px;
  font-weight: 500;
}
.list--partners__address{
  font-size: 14px;
  color: $color-black-faded;
}

