.list--partners__col-2{
  flex-basis: 100%;

  @include breakpoint(820){
    flex-basis: 15%;
    margin-left: auto;
  }
  margin-right: 34px;
}

.list--partners__col-3{
  flex-basis: 100%;

  @include breakpoint(820){
    flex-basis: 25%;
  }
}

.selected-content{
  opacity: 0;
  overflow: hidden;
  transition: all 500ms ease;
  max-height: 0;
  margin:0;
  padding:0;
}
.selected-content--visible{
  opacity: 1;
  max-height: 200vh;
}