/*------------------------------------*\
  #PRODUCTS
\*------------------------------------*/

.coverage__area {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-height: 60vh;
  overflow: hidden;
  transition: max-height 1000ms ease-in-out;

  &--expanded {
    max-height: 2000vh;
  }
}

.coverage__title {
  flex-basis: 100%;
}

.coverage__column {
  flex-basis: 100%;

  @include breakpoint(700) {
    flex-basis: 47%;
  }
}

.product-moto {
  margin-top: 120px;
  margin-bottom: 100px;

  h1, p, .moto-heading {
    max-width: 100%;

    @include breakpoint(1140) {
      max-width: 50%;
    }
  }
}

/*product highlights*/
.highlights {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  &--double {
    justify-content: space-evenly;
  }
}

.teaser--highlight {
  flex-basis: 100%;
  display: flex;
  flex-direction: column;

  .btn {
    min-width: 240px;
  }

  @include breakpoint-rev(820) {
    &:not(:last-child) {
      margin-bottom: 48px;
    }
  }

  @include breakpoint(700) {
    flex-basis: 48%;
  }

  @include breakpoint(820) {
    flex-basis: 29%;
  }
}

.teaser-highlight__text {
  margin-bottom: 48px;
  @include font-size(14);
  color: rgba(56,59,56, 0.6);
}

.teaser-icon {
  display: flex;
  width: 100px;
  height: 74px;
  margin-bottom: 30px;
  background-position-y: 100%;
  background-repeat: no-repeat;
}

.teaser-icon__microscope {
  background-image: url(../images/icons/health/microscope.svg);
}

.teaser-icon__stithoscope {
  background-image: url(../images/icons/health/stithoscope.svg);
}

.teaser-icon__program {
  background-image: url(../images/icons/health/program.svg);
}

.teaser-icon__pickup {
  background-image: url(../images/icons/home/pickup.svg);
}

.teaser-icon__support {
  background-image: url(../images/icons/home/support.svg);
}

.teaser-icon__flexibility {
  background-image: url(../images/icons/home/flexibility.svg);
}

.teaser-icon__discount {
  background-image: url(../images/icons/motor/discount.svg);
}

.teaser-icon__accidentcare {
  background-image: url(../images/icons/motor/accidentcare.svg);
}

.teaser-icon__settlement {
  background-image: url(../images/icons/motor/settlement.svg);
}

.teaser-icon__shield {
  background-image: url(../images/icons/life/shield.svg);
}

.teaser-icon__hourglass {
  background-image: url(../images/icons/life/hourglass.svg);
}

.teaser-icon__life-jacket {
  background-image: url(../images/icons/life/life-jacket.svg);
}

/*coverage*/
.coverage__title {
  padding-right: 0;

  @include breakpoint(1140) {
    padding-right: 50%;
  }
}

.teaser__btn {
  margin-top: auto;
}

.coverage__yes {

  li {
    margin-bottom: 40px;
  }

  > ul {
    padding-left: 30px;

    > li {
      position: relative;

      &:before {
        content: '';
        position: absolute;
        left: -30px;
        top: 0;
        background-image: url(../images/icons/check-blue.svg);
        background-repeat: no-repeat;
        background-size: 100%;
        width: 24px;
        height: 24px;
      }
    }
  }
}

.coverage__no {

  li {
    margin-bottom: 40px;
  }

  > ul {
    padding-left: 30px;

    > li {
      position: relative;

      &:before {
        content: '';
        position: absolute;
        left: -30px;
        top: 1px;
        background-color: #000;
        width: 22px;
        height: 22px;
        border-radius: 50%;
      }

      &:before {
        content: '';
        position: absolute;
        left: -26px;
        right: 0;
        top: 11px;
        background-color: #fff;
        width: 14px;
        height: 2px;
      }
    }
  }
}

/*reasonging slider*/
.reasoning__menu {
  ul {
    display: flex;
    justify-content: center;
  }

  a {
    display: block;
    padding: 24px 64px;
    font-size: 18px;
    border-bottom: 3px solid transparent;
    cursor: pointer;
  }

  .slick-active {
    a {
      border-color: $color-base;
      font-weight: 500;
    }
  }
}


.reasoning__text {
  margin-bottom: 45px;
  text-align: center;
  font-size: 18px;
}

/*grid*/
.program-grid {
  display: grid;
  margin-bottom: 40px;
  grid-template-columns: auto;

  @include breakpoint(540) {
    grid-template-columns: 183px auto;
  }
}


.grid-table-column {
  display: grid;
  align-items: center;
  grid-template-rows: auto;
}

.program-attributes {
  font-size: 14px;
  display: none;

  @include breakpoint(540) {
    display: grid;
  }

  .grid-table-cell {
    padding-left: 0;
    padding-right: 0;
    min-height: 90px;
    align-items: center;

    @include breakpoint(540) {
      display: flex;
    }
  }
}

.grid-table {
  display: grid;
  grid-column-gap: 32px;
  margin: 0 auto;
  align-items: top;
  grid-template-columns: repeat(var(--programCount), calc(100vw - 120px));

  @include breakpoint(540) {
    grid-template-columns: repeat(var(--programCount), 274px);
  }

  border-radius: 0.5rem;
  transform: translateX(var(--transform));
  transition: transform 500ms ease-in-out;
}

@include breakpoint(540) {
  .program-grid-2 {
    .grid-table {
      grid-template-columns: repeat(var(--programCount), 428px);
    }
  }
}

.grid-table-window {
  overflow: hidden;
  padding-left: 0;

  @include breakpoint(540) {
    padding-left: 29px;
    border-left: 1px solid #eee;
  }

  @include breakpoint-rev(1140) {
    overflow-x: scroll;
  }
}

.grid-table-cell {
  padding: 4px 20px;

  @include breakpoint-rev(540) {
    border-bottom: 1px solid #b5b5b5;

    &:before {
      content: attr(data-title);
      display: block;
      margin-bottom: 4px;
      color: $color-black-faded;
      font-size: 14px;
    }
  }
}

.program {
  align-items: center;
  background-color: #eee;

  .grid-table-cell {
    font-weight: 500;
    line-height: 1.2;
    min-height: 90px;
    align-items: center;
    justify-content: center;

    @include breakpoint(540) {
      display: flex;
    }
  }

  .grid-table-cell--heading {
    min-height: 80px;
  }
}

.grid-table-cell--heading {
  height: 100%;
  border-radius: 0 0 25px 0;
  color: #fff;
  line-height: 1.3;
  font-weight: 500;
  font-size: 18px;
  padding: 20px;
}

.program-grid:not(.program-grid--subsection) {
  > .program-attributes {
    .grid-table-cell {
      &:first-child {
        min-height: 80px;
      }
    }
  }
}

