.service-provided {
  position: relative;

  &:before {
    content: '';
    display: block;
    background-image: url(../images/icons/check-blue.svg);
    background-repeat: no-repeat;
    background-size: 100%;
    width: 24px;
    height: 24px;
    //margin:0 auto;
  }
}

.service-not-provided {
  position: relative;

  &:before {
    content: '';
    margin: 0 auto;
  }
}

/*controls*/
.theme-health {
  .program-controls {
    &__left, &__right{
      display:block;
    }
  }
}

.program-controls {
  padding-left: 212px;
  display: flex;
  justify-content: flex-end;
  margin-top: 54px;

  &__more {
    flex-basis: calc(100% - 104px);
    padding: 10px 14px 8px;
    border: 1px solid $dusky-blue;
    border-radius: 4px;
    font-size: 12px;
    letter-spacing: 0.3px;
    font-weight: 500;
    color: $dusky-blue;
    line-height: 1;
    cursor: pointer;
    flex-grow: 1;
    max-height: 35px;
  }

  &__left {
    cursor: pointer;
    margin-left: 24px;
  }

  &__right {
    cursor: pointer;
    margin-left: 8px;
  }
}

.icon-down {
  float: right;

  svg {
    max-width: 16px;
    max-height: 16px;
  }
}

.no-touch {
  .program-controls__left:hover {
    g {
      opacity: 1;
    }
  }
}


/*filtering*/
.filter {
  position: relative;
  margin-bottom: 30px;
  text-align: right;
}

.filter__trigger {
  display: inline-block;
  padding: 10px 14px 9px;
  border: 1px solid $dusky-blue;
  background-color: $dusky-blue;
  color: #fff;
  border-radius: 4px;
  font-size: 12px;
  font-family: inherit;
  cursor: pointer;
  text-align: center;
}

.filter__results {
  box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.18);
  position: absolute;
  top: 50px;
  right: 0;
  opacity: 0;
  padding: 24px;
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  z-index: -1;
  transform: translateY(40px);
  transition: opacity 300ms ease-in-out, transform 400ms ease-in-out, z-index 0.5s step-end;
  background-color: #fff;
}

.filter__results--active {
  opacity: 1;
  z-index: 1;
  transform: translateY(0);
  transition: opacity 300ms ease-in-out, transform 400ms ease-in-out, z-index 0.5s step-start;
}

.filter__results__column {
  flex-basis: 33%;
}

.filter__results__heading {
  min-height: 23px;
  margin-bottom: 24px;
  font-weight: 800;
  font-size: 14px;
  line-height: 1;
}

.filter__item {
  margin-bottom: 16px;
}

.filter__actions {
  flex-basis: 100%;
  display: flex;
  flex-wrap: wrap;
}

.filter__cancel {
  margin-left: auto;
  margin-right: 16px;
}

.program {
  display: none;
}

.program--visible {
  display: inherit;
}

.program-grid--subsection {
  margin-bottom: 24px;

  .program-attributes {
    font-size: 11px;
  }
}

.program-grid--subsection__title {
  background: $color-base-grey;
  color: $charcoal;
