  padding: 13px 16px;
  font-weight: 500;
}

.program-heading {
  border-left: 1px solid #eee;
  margin-left: 0;
  padding-left: 0;

  @include breakpoint(540) {
    margin-left: 183px;
    padding-left: 30px;
  }

  h2 {
    padding: 16px 20px;
    margin: 0;
    font-size: 16px;
    background-color: #eee;
  }
}

.program-grid--subsection .program {
  background-color: transparent;
}

.grid-table-cell--expander {
  padding-top: 16px;
  padding-bottom: 20px;
  font-size: 12px;
  color: $dusky-blue;
  text-align: center;
  display: inherit;

  @include breakpoint(540) {
    display: none !important;
  }
}

.program-expander {
  max-height: 0;
  transition: max-height 400ms ease;
  overflow: hidden;

  &.program-expander--visible {
    max-height: 200vh;
  }
}

main {
  overflow: hidden;
}


.contentgroup {
  display: flex;
  flex-wrap: wrap;

  ul {
    margin-bottom: 24px;
    padding-bottom: 24px;
    margin-left: 14px;
    border-bottom: 1px solid #eee;
  }

  &:last-child {
    ul {
      margin-bottom: 24px;
      padding-bottom: 24px;


      &:last-child {
        border: 0;
      }
    }
  }
}

.contentgroup__tick {
  li {
    margin-bottom: 8px;
    position: relative;
    font-size: 14px;

    &:before {
      content: '';
      position: absolute;
      left: -30px;
      top: 1px;
      background-image: url("data:image/svg+xml;utf8,<svg viewBox='0 0 64 64' xmlns='http://www.w3.org/2000/svg'><path fill='%23f2b100' d='M21.33,57.82,0,36.53l5.87-5.87L21.33,46.09,58.13,9.36,64,15.23,21.33,57.82'/></svg>");
      background-repeat: no-repeat;
      background-size: 16px;
      width: 24px;
      height: 24px;
    }
  }
}

.contentgroup__left {
  padding-bottom: 20px;
  flex-basis: 150px;
  margin-right: 24px;
  border-right: 1px solid #eee;
}

.contentgroup__right {
}

.contentgroup__title {
  margin-bottom: 4px;
  font-size: 14px;
  font-weight: 500;
  color: $color-home;
}

.theme-motor {
